  opacity: 0.3;
  filter: alpha(opacity=30);
  position: fixed;
  left: 0;
  top: 0;
  z-index: 10001;
}
.dialog {
  display: none;
  border-radius: 10px;
  background-color: #FFF;
  position: fixed;
  left: 50%;
  top: 50%;
  z-index: 10002;
}
.dialog .icon {
